@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700");
/* Open Sans font-family*/

*,
*:before,
*:after {
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

body {
  margin: 0 auto;
}

