.navigation {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 355px;
  background-color: #3D4196;
  background-image: radial-gradient(circle closest-corner at 75% 50%, #3F489F 100%, transparent 50%);
}

.navigation__info-panel {
  display: block;
  max-width: 600px;
  padding-top: 42px;
}

.navigation__button {
  display: inline-block;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  margin-right: 87px;
}

.navigation__button:hover {
  transform: scale(1.1);
  transition: 0.5s;
}

.navigation__button.-full-screen {
  background: url("../assets/full-size.svg") 100%, no-repeat;
  background-size: cover;
  width: 36px;
  height: 36px;
  margin-left: 20px;
}

.navigation__button.-message {
  background: url("../assets/chat.svg") 100%, no-repeat;
  background-size: cover;
  width: 36px;
  height: 36px;
}

.navigation__button.-notification {
  margin-right: 160px;
  background: url("../assets/notification.svg") 100%, no-repeat;
  background-size: cover;
  width: 36px;
  height: 36px;
}

.navigation__button.-menu {
  background: url("../assets/menu.svg") 100%, no-repeat;
  background-size: cover;
  width: 36px;
  height: 36px;
}

.navigation__button-unread {
  width: 28px;
  height: 28px;
  margin-top: -30px;
  margin-left: 14px;
  padding-top: 6px;
  text-align: center;
  position: absolute;
  color: white;
  background-color: #E83D35;
  border-radius: 50%;
}

.navigation__button-unread.-bell {
  margin-left: 7px;
}

@media all and (max-width: 720px) {
  .navigation {
    height: 255px;
  }
}

@media all and (max-width: 600px) {
  .navigation__info-panel {
    display: block;
    max-width: 100%;
  }

  .navigation__button {
    margin-right: 30px;
    zoom: 0.7;
  }
}

