@charset "UTF-8";

.creating-flat {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  position: relative;
  margin-top: -150px;
}

.creating-flat__container {
  display: block;
  width: 370px;
  min-height: 1750px;
  background-color: #F2F2F3;
  border: 1px solid #E4E3E3;
  border-radius: 5px;
  margin-right: 30px;
}

.creating-flat__grid-container {
  display: grid;
  width: 1490px;
  margin-bottom: 20px;
  padding: 0 40px 10px;
  background-color: #ffffff;
  border: 1px solid #E4E3E3;
  border-radius: 5px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 680px));
  grid-template-rows: 120px 510px 550px;
  grid-auto-rows: 5px;
  grid-column-gap: 40px;
}

.creating-flat__nav-buttons {
  grid-column-start: 1;
  grid-column-end: -1;
  border-bottom: 2px solid #E4E3E3;
}

.creating-flat__banner {
  margin-top: 52px;
  margin-bottom: 44px;
  font-size: 20px;
  font-weight: 600;
  color: #92A2F7;
  text-transform: uppercase;
}

.creating-flat__button-back {
  margin-right: 26px;
  padding: 16px 30px;
  color: #7A7B80;
  background-color: transparent;
  font-weight: 600;
  font-size: 20px;
  border: 2px solid #E4E3E3;
  border-radius: 5px;
  text-transform: capitalize;
  cursor: pointer;
}

.creating-flat__button-create {
  padding: 16px 62px;
  color: #ffffff;
  background-color: #647AF4;
  font-weight: 600;
  font-size: 20px;
  border: 2px solid transparent;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
}

.creating-flat__buttons-container {
  position: relative;
  float: right;
  margin-top: -90px;
  clear: right;
}

.creating-flat__form-info {
  padding-left: 52px;
  color: #7A7B80;
  font-weight: 700;
  font-size: 18px;
}

.creating-flat__form-info:before {
  display: block;
  margin-left: -50px;
  padding: 0 8px;
  position: absolute;
  font-weight: 400;
  color: #7A7B80;
  background-color: #F8F8F8;
  border: 1px solid #7A7B80;
  border-radius: 50%;
}

.creating-flat__form-info.-first-column:before {
  content: "1";
}

.creating-flat__form-info.-second-column:before {
  content: "2";
}

.creating-flat__form-info.-third-column {
  grid-column-start: 1;
  grid-column-end: -1;
}

.creating-flat__form-info.-third-column:before {
  content: "3";
}

.creating-flat__input-field {
  width: 320px;
  margin-bottom: 50px;
  margin-left: 14px;
  padding: 22px 20px 16px;
  font-size: 20px;
  font-weight: 400;
  border: none;
  border-bottom: 1px solid #7A7B80;
  border-radius: 2px;
  color: #9B9CA0;
  background-color: #F8F8F8;
}

.creating-flat__input-field.-small {
  width: 300px;
  margin-left: 35px;
}

.creating-flat__input-field:nth-child(2) {
  width: 660px;
}

.creating-flat__input-field:last-child {
  width: 660px;
  border-bottom: 3px solid #647AF4;
}

.creating-flat__warning {
  display: block;
  margin-top: -40px;
  padding-left: 30px;
  width: 100px;
  height: 20px;
  position: absolute;
  content: "*Обязательно";
  font-size: 14px;
  font-weight: 400;
  color: #9B9CA0;
  z-index: 10;
}

.creating-flat__info-message {
  margin-top: 50px;
  margin-bottom: 90px;
  padding-left: 30px;
  color: #7A7B80;
  font-weight: 700;
  font-size: 18px;
}

.creating-flat__tumbler {
  margin-top: 90px;
}

.creating-flat__tumbler input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}

.creating-flat__tumbler-text {
  font-size: 20px;
  font-weight: 400;
  color: #9B9CA0;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}

.creating-flat__tumbler-text:before {
  content: '';
  position: relative;
  z-index: 3;
  float: right;
  width: 70px;
  height: 26px;
  border-radius: 13px;
  background-color: #F7F7F7;
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.2);
  transition: 0.2s;
}

.creating-flat__tumbler-text:after {
  display: block;
  content: '';
  position: relative;
  z-index: 10;
  float: right;
  margin-top: -8px;
  left: 40px;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background-color: #F2F2F2;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  transition: 0.2s;
}

.creating-flat__tumbler input:checked + .creating-flat__tumbler-text:before {
  background-color: #C4CDFF;
}

.creating-flat__tumbler input:checked + .creating-flat__tumbler-text:after {
  left: 72px;
  background-color: #647AF4;
}

.creating-flat__active-meters {
  border-top: 2px solid #E4E3E3;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 680px));
  grid-column-gap: 40px;
  grid-column-start: 1;
  grid-column-end: -1;
}

.creating-flat__mark-meter {
  display: block;
  width: 685px;
  margin: 60px 0;
  padding: 11px 23px;
  background-color: #F8F8F8;
  border-radius: 33px;
}

.creating-flat__mark-meter.-electro {
  display: inherit;
  width: 203px;
  margin-right: 100px;
}

.creating-flat__mark-meter.-electro-twin {
  display: inherit;
  width: 340px;
}

.creating-flat__mark-meter.-heat-power {
  display: inherit;
  width: 185px;
}

.creating-flat__mark-meter.-water {
  display: inherit;
  width: 170px;
}

.creating-flat__mark-meter.-water-warm {
  display: inherit;
  width: 320px;
}

.creating-flat__mark-meter.-outfall {
  display: inherit;
  width: 205px;
}

.creating-flat__mark-meter.-gas {
  display: inherit;
  width: 125px;
}

.creating-flat__active-meters-box {
  display: block;
  max-width: 685px;
  margin-top: 35px;
}

@media all and (max-width: 1900px) {
  .creating-flat {
    justify-content: center;
  }

  .creating-flat__container:first-child {
    display: none;
  }
}

@media all and (max-width: 1500px) {
  .creating-flat__grid-container {
    width: 720px;
    padding: 0 20px 10px;
    grid-template-rows: 120px 510px 350px 550px;
  }

  .creating-flat__input-field.-small {
    margin-left: 35px;
  }

  .creating-flat__input-field:nth-child(2) {
    width: 660px;
  }

  .creating-flat__input-field:last-child {
    width: 660px;
  }

  .creating-flat__add-plug-meter {
    border-top: 2px solid #E4E3E3;
  }
}

@media all and (max-width: 720px) {
  .creating-flat__input-field {
    width: 290px;
    margin-left: 0;
  }

  .creating-flat__input-field.-small {
    width: 290px;
    margin-left: 0;
  }

  .creating-flat__input-field:nth-child(2) {
    width: 290px;
  }

  .creating-flat__input-field:last-child {
    width: 290px;
  }

  .creating-flat__input-field:nth-child(n+2) {
    margin-left: 20px;
  }
}

@media all and (max-width: 690px) {
  .creating-flat__grid-container {
    grid-template-rows: 160px 740px 280px 590px;
  }

  .creating-flat__banner {
    margin-top: 22px;
  }

  .creating-flat__buttons-container {
    display: inline-block;
    float: none;
  }

  .creating-flat__button-back,
  .creating-flat__button-create {
    padding: 10px;
    font-size: 16px;
  }

  .creating-flat__info-message {
    margin-bottom: 40px;
    padding-left: 0;
  }

  .creating-flat__tumbler-text {
    font-size: 16px;
    padding-left: 0;
  }

  .creating-flat__mark-meter {
    padding-left: 23px;
  }
}

@media all and (max-width: 460px) {
  .creating-flat__grid-container {
    padding: 9px;
    grid-template-rows: 160px 740px 320px 880px;
  }

  .creating-flat__input-field:nth-child(n+2) {
    margin-left: 0;
  }

  .creating-flat__tumbler-text:before {
    position: absolute;
    float: none;
    margin-top: 50px;
  }

  .creating-flat__tumbler-text:after {
    position: absolute;
    float: none;
    margin-top: 20px;
    left: 0;
  }

  .creating-flat__tumbler input:checked + .creating-flat__tumbler-text:after {
    left: 32px;
  }

  .creating-flat__active-meters-box {
    margin: 35px 0 50px;
  }

  .creating-flat__mark-meter {
    padding: 5px;
    background-color: #e4e8ff;
  }
}

